.top {
    width: 100%;
    height: 50px;
    background-color: #8881814d;
    position: sticky;
    top: 0;
    display: flex;
    align-items: center;
    font-family: 'Gemunu Libre', sans-serif;



}

.topleft {
    flex: 7;


}

.topicons {
    margin: 80px;
    cursor: pointer;
}

.topcenter {


    flex: 1;
    cursor: pointer;
}

.topright {
    flex: 0;
    margin-right: 30px;
    display: flex;
    cursor: pointer;
}